'use client'
import { assets } from "@/assets/assets"
import { useAuth } from "@clerk/nextjs"
import axios from "axios"
import Image from "next/image"
import { useState } from "react"
import { toast } from "react-hot-toast"
import Loading from "@/components/Loading"
import { UploadCloud, X } from "lucide-react"

export default function StoreAddProduct() {
    const [images, setImages] = useState({})
    const [productInfo, setProductInfo] = useState({
        name: "", 
        description: "", 
        mrp: "", 
        price: "", 
        category: "", 
        stock: "" // Added Stock field
    })
    const [loading, setLoading] = useState(false)
    const { getToken } = useAuth()

    const categories = ['Fresh','Upvas','special']

    const handleImageUpload = (key, file) => {
        if (file) {
            // Validation: Check file size (2MB = 2 * 1024 * 1024 bytes)
            if (file.size > 2 * 1024 * 1024) {
                toast.error(`Image ${key} is too large! Max size is 2MB.`)
                return
            }
            setImages(prev => ({ ...prev, [key]: file }))
        }
    }

    const onSubmitHandler = async (e) => {
        e.preventDefault()
        try {
            if (Object.keys(images).length === 0) return toast.error('Please upload at least one image')
            
            setLoading(true)
            const formData = new FormData()
            
            formData.append('name', productInfo.name)
            formData.append('description', productInfo.description)
            formData.append('mrp', productInfo.mrp)
            formData.append('price', productInfo.price)
            formData.append('category', productInfo.category)
            formData.append('stock', productInfo.stock) // Sending stock data

            Object.keys(images).forEach((key) => { 
                formData.append('images', images[key]) 
            })

            const token = await getToken()
            await axios.post('/api/store/product', formData, { headers: { Authorization: `Bearer ${token}` } })
            
            toast.success("Product added successfully!")
            // Reset Form
            setProductInfo({ name: "", description: "", mrp: "", price: "", category: "", stock: "" })
            setImages({})
        } catch (error) {
            toast.error(error?.response?.data?.error || error.message)
        } finally {
            setLoading(false)
        }
    }

    if (loading) return <Loading />

    return (
        <form onSubmit={onSubmitHandler} className="max-w-4xl mb-20 text-slate-800">
            <h1 className="text-2xl text-slate-500 mb-6">Add New <span className="text-slate-800 font-medium">Product</span></h1>
            
            <div className="flex flex-wrap gap-4 mb-8">
                {[1, 2, 3, 4].map((num) => (
                    <label key={num} className="cursor-pointer group relative">
                        <div className={`w-32 h-32 flex items-center justify-center border-2 border-dashed rounded-lg transition-colors ${images[num] ? 'border-green-500 bg-green-50' : 'border-slate-300 hover:border-slate-400'}`}>
                            {images[num] ? (
                                <Image src={URL.createObjectURL(images[num])} alt="" width={100} height={100} className="w-full h-full object-cover rounded-lg" />
                            ) : (
                                <div className="text-center p-2">
                                    <UploadCloud className="mx-auto text-slate-400 mb-1" />
                                    <span className="text-xs text-slate-500">Upload {num}</span>
                                </div>
                            )}
                        </div>
                        <input 
                            type="file" 
                            hidden 
                            accept="image/*" 
                            onChange={(e) => handleImageUpload(num, e.target.files[0])} 
                        />
                        {images[num] && (
                            <button 
                                type="button"
                                onClick={(e) => { e.preventDefault(); const newImages = {...images}; delete newImages[num]; setImages(newImages); }}
                                className="absolute -top-2 -right-2 bg-red-500 text-white rounded-full p-1 shadow-sm hover:bg-red-600"
                            >
                                <X size={12} />
                            </button>
                        )}
                    </label>
                ))}
            </div>
            <p className="text-xs text-slate-500 mb-8">* Max image size: 2MB per file.</p>

            <div className="grid md:grid-cols-2 gap-6 mb-6">
                <div className="space-y-4">
                    <label className="block">
                        <span className="text-sm font-medium">Product Name</span>
                        <input 
                            type="text" 
                            required 
                            className="w-full mt-1 p-2 border border-slate-200 rounded-md focus:ring-2 focus:ring-slate-800 outline-none" 
                            value={productInfo.name}
                            onChange={e => setProductInfo({...productInfo, name: e.target.value})}
                        />
                    </label>
                    <label className="block">
                        <span className="text-sm font-medium">Description</span>
                        <textarea 
                            required 
                            rows={4}
                            className="w-full mt-1 p-2 border border-slate-200 rounded-md focus:ring-2 focus:ring-slate-800 outline-none" 
                            value={productInfo.description}
                            onChange={e => setProductInfo({...productInfo, description: e.target.value})}
                        />
                    </label>
                    <label className="block">
                        <span className="text-sm font-medium">Category</span>
                        <select 
                            required 
                            className="w-full mt-1 p-2 border border-slate-200 rounded-md focus:ring-2 focus:ring-slate-800 outline-none" 
                            value={productInfo.category}
                            onChange={e => setProductInfo({...productInfo, category: e.target.value})}
                        >
                            <option value="">Select Category</option>
                            {categories.map(c => <option key={c} value={c}>{c}</option>)}
                        </select>
                    </label>
                </div>

                <div className="space-y-4">
                    <div className="grid grid-cols-2 gap-4">
                        <label className="block">
                            <span className="text-sm font-medium">MRP</span>
                            <input 
                                type="number" 
                                required 
                                className="w-full mt-1 p-2 border border-slate-200 rounded-md focus:ring-2 focus:ring-slate-800 outline-none" 
                                value={productInfo.mrp}
                                onChange={e => setProductInfo({...productInfo, mrp: e.target.value})}
                            />
                        </label>
                        <label className="block">
                            <span className="text-sm font-medium">Selling Price</span>
                            <input 
                                type="number" 
                                required 
                                className="w-full mt-1 p-2 border border-slate-200 rounded-md focus:ring-2 focus:ring-slate-800 outline-none" 
                                value={productInfo.price}
                                onChange={e => setProductInfo({...productInfo, price: e.target.value})}
                            />
                        </label>
                    </div>
                    <label className="block">
                        <span className="text-sm font-medium">Initial Stock Quantity</span>
                        <input 
                            type="number" 
                            required 
                            placeholder="e.g. 50"
                            className="w-full mt-1 p-2 border border-slate-200 rounded-md focus:ring-2 focus:ring-slate-800 outline-none" 
                            value={productInfo.stock}
                            onChange={e => setProductInfo({...productInfo, stock: e.target.value})}
                        />
                    </label>
                </div>
            </div>

            <button disabled={loading} className="bg-slate-900 text-white px-8 py-3 rounded-md hover:bg-slate-800 transition-colors w-full md:w-auto">
                {loading ? "Adding Product..." : "Add Product"}
            </button>
        </form>
    )
}